import Swiper from "swiper/bundle";

const specialtyDrinks = document.querySelector(".specialty-drinks");

if (specialtyDrinks) {
  const swiper = new Swiper(".specialty-drinks__slider", {
    speed: 800,
    slidesPerView: 3,
    slidesPerGroup: 3,
    allowTouchMove: false,
    navigation: {
      nextEl: ".swiper-button-next",
      prevEl: ".swiper-button-prev",
    },
    breakpoints: {
      200: {
        allowTouchMove: true,
        slidesPerView: 1,
        slidesPerGroup: 1,
      },
      1100: {
        allowTouchMove: false,
        spaceBetween: 80,
      },
      1400: {
        slidesPerView: 3,
        spaceBetween: 80,
      },
      1650: {
        spaceBetween: 190,
      },
    },
  });

  const title = specialtyDrinks.querySelector(".specialty-drinks__title");

  swiper.on("slideChange", (slide) => {
    if (window.matchMedia("(max-width: 1400px)").matches) {
      switch (slide.activeIndex) {
        case 0:
          title.innerHTML = "Фирменные напитки";
          break;
        case 2:
          title.innerHTML = "Фирменные напитки";
          break;
        case 3:
          title.innerHTML = "Сезонное меню";
          break;
      }
    } else {
      switch (slide.activeIndex) {
        case 0:
          title.innerHTML = "Фирменные напитки";
          break;
        case 3:
          title.innerHTML = "Сезонное меню";
          break;
      }
    }
  });
}
