import { Loader } from "@googlemaps/js-api-loader";

const loader = new Loader({
  apiKey: "AIzaSyCLf_eYKNS4TVVz_8uoImQWbM2-mXCyo78",
  version: "weekly",
});

loader.load().then(() => {
  const map = new google.maps.Map(document.getElementById("map"), {
    center: { lat: 59.932045, lng: 30.355115 },
    zoom: 15,
  });

  const marker = new google.maps.Marker({
    position: { lat: 59.932045, lng: 30.355115 },
    map: map,
    icon: "../img/location-pin.svg",
    draggarble: false,
  });

  const information = new google.maps.InfoWindow({
    content: "Невский 98, лит А",
  });

  marker.addListener("click", function () {
    information.open(map, marker);
  });
});
