const balance = document.querySelector(".balance");

if (balance) {
  const buttonOpen = balance.querySelector(".balance__btn");
  const buttonClose = balance.querySelector(".balance__btn-close");
  const modal = balance.querySelector(".balance__modal");
  const body = document.querySelector("body");

  const onWindowClick = (evt) => {
    if (!evt.target.matches(".balance__modal, balance__modal *")) {
      modal.classList.remove("balance__modal_opened");
      body.classList.remove("no-scroll");
      document.removeEventListener("click", onWindowClick);
    }
  };

  buttonOpen.addEventListener("click", (event) => {
    event.stopPropagation();
    modal.classList.add("balance__modal_opened");
    body.classList.add("no-scroll");
    document.addEventListener("click", onWindowClick);
  });

  buttonClose.addEventListener("click", () => {
    modal.classList.remove("balance__modal_opened");
    body.classList.remove("no-scroll");
  });
}
