const drinks = document.querySelector(".drinks");

if (drinks) {
  const buttonOpen = drinks.querySelector(".drinks__content-btn");
  const buttonClose = drinks.querySelector(".drinks__btn-close");
  const modal = drinks.querySelector(".drinks__modal");
  const body = document.querySelector("body");

  const onWindowClick = (evt) => {
    if (!evt.target.matches(".drinks__modal, drinks__modal *")) {
      modal.classList.remove("drinks__modal_opened");
      body.classList.remove("no-scroll");
      document.removeEventListener("click", onWindowClick);
    }
  };

  buttonOpen.addEventListener("click", (event) => {
    event.stopPropagation();
    modal.classList.add("drinks__modal_opened");
    body.classList.add("no-scroll");
    document.addEventListener("click", onWindowClick);
  });

  buttonClose.addEventListener("click", () => {
    modal.classList.remove("drinks__modal_opened");
    body.classList.remove("no-scroll");
  });
}
