const header = document.querySelector(".header");

if (header) {
  const anchors = header.querySelectorAll(".header__link");
  const burger = header.querySelector(".header__nav-button");
  const nav = header.querySelector(".header__nav");
  const body = document.querySelector("body");

  const onWindowClick = (evt) => {
    if (
      !evt.target.matches(
        ".header__nav, header__nav *, header__nav-button, header__nav-button *"
      )
    ) {
      nav.classList.remove("header__nav_opened");
      body.classList.remove("no-scroll");
      document.removeEventListener("click", onWindowClick);
    }
  };

  burger.addEventListener("click", (event) => {
    event.stopPropagation();
    nav.classList.toggle("header__nav_opened");
    body.classList.toggle("no-scroll");
    document.addEventListener("click", onWindowClick);
  });

  anchors.forEach((anchor) =>
    anchor.addEventListener("click", (e) => {
      e.preventDefault();

      var href = anchor.getAttribute("href");
      var elem =
        document.querySelector(href) ||
        document.querySelector(
          "a[name=" + href.substring(1, href.length) + "]"
        );

      window.scroll({
        top: elem.offsetTop,
        left: 0,
        behavior: "smooth",
      });
    })
  );
}
